<template>
  <div class="dt">
    <div id="container" class="bddt" >
    
  </div>
  </div>
  
</template>

<script>
export default {
  data() {
      return {
        jwdlist:[
          {id:1,jind:104.594022,weid:31.094896,title:"本地"},
          {id:2,jind:102.24201,weid:34.97842,title:"德阳市位置"},
          {id:3,jind:104.597052,weid:31.094896,title:"文本内容标"},
          {id:4,jind:104.598062,weid:31.094896,title:"文本内容标"},
          {id:5,jind:104.599072,weid:31.094896,title:"文本内容标"},
          // 104.594022,31.094896
        ]
      }
  },
  mounted(){
    var map = new window.BMapGL.Map("container");
    var point = new window.BMapGL.Point(104.594022, 31.094896);
    map.centerAndZoom(point, 15); 
    var marker = new BMapGL.Marker(point);    
    map.addOverlay(marker)
    for (var i = 0; i <this.jwdlist.length;i++) {
      var marker = new window.BMapGL.Marker(new window.BMapGL.Point(this.jwdlist[i].jind, this.jwdlist[i].weid));
      // marker.title = this.jwdlist[i].title;
      map.addOverlay(marker);
    }
    
    marker.addEventListener("click", function(){   
      alert("您当前的位置");   
    });
    // 5. 开启鼠标滚轮缩放
    map.enableScrollWheelZoom(true)
    var scaleCtrl = new window.BMapGL.ScaleControl() // 添加比例尺控件
    map.addControl(scaleCtrl)
    var zoomCtrl = new window.BMapGL.ZoomControl() // 添加缩放控件
    map.addControl(zoomCtrl)
    var cityCtrl = new window.BMapGL.CityListControl() // 添加城市列表控件
    map.addControl(cityCtrl)
    // map.setHeading(64.5);   //设置地图旋转角度
    // map.setTilt(73);       //设置地图的倾斜角度
  //   function myFun(result){
  //       var cityName = result.name;
  //       map.setCenter(cityName);
  //       alert("当前定位城市:" + cityName);
  //   }
  //   var myCity = new BMapGL.LocalCity();
  //   myCity.get(myFun); 
  } 
}
</script>

